body {
    width: 800px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    display: inline;
}
.header {
    h1 {
        font-size: 48px;
    }
}
.dataset-list {
    position: fixed;
    left: 50%;
    margin-left: -180px;
}
.dataset {
    margin-top: 10px;
    font-size: 32px;
    padding: 30px 50px;
    width: 250px;
    border: 3px solid maroon;
    border-radius: 25px;
}
.dataset:hover {
    background-color: lightcoral;
}